@import url(https://fonts.googleapis.com/css2?family=Cookie&display=swap);
body {
	margin: 0;
	background-color: #e8f0a0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@media (min-width: 600px) {
	.app {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		min-height: 100vh;
		margin-top: 0;
	}
}

.app {
	max-width: 1200px;
	margin-top: 40px;
	margin-right: auto;
	margin-left: auto;
	padding-right: 16px;
	padding-left: 16px;
	text-align: center;
}

.logo img {
	width: 800px;
	max-width: 100%;
	margin-bottom: 40px;
	transition: width 1s ease-in-out;
}

.app.video .logo img {
	width: 400px;
}

h1 {
	padding: 0 32px;
	color: #515542;
	font-family: 'Cookie', cursive;
	font-size: 70px;
}

.iframe-container {
	position: relative;
	width: 100%;
	padding-top: 56.25%;
	overflow: hidden;
	border-radius: 12px;
	-webkit-animation: zoom 1s ease-in-out 1;
	        animation: zoom 1s ease-in-out 1;
}

.iframe-container iframe {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

@-webkit-keyframes zoom {
	from {
		-webkit-transform: scale(0);
		        transform: scale(0);
	}
	to {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

@keyframes zoom {
	from {
		-webkit-transform: scale(0);
		        transform: scale(0);
	}
	to {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

